import { Routes, Route } from 'react-router-dom'
import { useAuthStore } from '@/store/authStore'
import { useThemeStore } from '@/store/themeStore'
import { useEffect } from 'react'
import { GoogleOAuthProvider } from '@react-oauth/google'

// 
import HomePage from './pages/HomePage'
import LoginPageNew from './pages/LoginPageNew'
import VerificationPage from './pages/VerificationPage'
import OverviewPage from './pages/OverviewPage'
import StrategiesPage from './pages/StrategiesPage'
//  - 
// import StrategyTemplatesPage from './pages/StrategyTemplatesPage'
import StrategyLiveDetailsPage from './pages/StrategyLiveDetailsPage'
import LiveStrategiesPage from './pages/LiveStrategiesPage'
import BacktestPage from './pages/BacktestPage'
import BacktestDetailsPage from './pages/BacktestDetailsPage'
import AIChatPage from './pages/AIChatPage'
import APIManagementPage from './pages/APIManagementPage'
// TradingPageTradingNotesPage - 1.0
// import TradingPage from './pages/TradingPage'
// import TradingNotesPage from './pages/TradingNotesPage'
import ProfilePage from './pages/ProfilePage'
import AdminDashboardPage from './pages/AdminDashboardPage'
import UserManagementPage from './pages/UserManagementPage'
import ClaudeManagementPage from './pages/ClaudeManagementPage'
import WalletManagementPage from './pages/WalletManagementPage'
import DataManagementPageReal from './pages/DataManagementPageReal'
import NotFoundPage from './pages/NotFoundPage'

// 
import MainLayout from './components/layout/MainLayout'
import AuthLayout from './components/layout/AuthLayout'

// 
import ProtectedRoute from './components/common/ProtectedRoute'
import LoadingSpinner from './components/common/LoadingSpinner'

function App() {
  const { isLoading, checkAuth, token, isAuthenticated } = useAuthStore()
  const { theme } = useThemeStore()

  // 
  useEffect(() => {
    // tokencheckAuth
    // token
    if (token && !isAuthenticated) {
      checkAuth()
    }
  }, [checkAuth, token, isAuthenticated])

  // 
  useEffect(() => {
    if (theme === 'dark') {
      document.documentElement.classList.add('dark')
    } else {
      document.documentElement.classList.remove('dark')
    }
  }, [theme])

  // 
  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900 transition-colors">
        <LoadingSpinner size="lg" />
      </div>
    )
  }

  return (
    <GoogleOAuthProvider clientId={import.meta.env.VITE_GOOGLE_CLIENT_ID || "temp_client_id_for_development"}>
      <Routes>
        {/*  - AuthLayout */}
        <Route path="/login" element={<LoginPageNew />} />
      
      <Route path="/verification" element={
        <AuthLayout>
          <VerificationPage />
        </AuthLayout>
      } />

      {/*  - AI */}
      <Route path="/" element={<HomePage />} />

      {/*  -  */}
      <Route path="/overview" element={
        <ProtectedRoute>
          <MainLayout>
            <OverviewPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      <Route path="/strategies" element={
        <ProtectedRoute>
          <MainLayout>
            <StrategiesPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      {/*  -  */}
      {/* <Route path="/strategy-templates" element={
        <ProtectedRoute>
          <MainLayout>
            <StrategyTemplatesPage />
          </MainLayout>
        </ProtectedRoute>
      } /> */}

      <Route path="/strategy/:strategyId/live" element={
        <ProtectedRoute>
          <MainLayout>
            <StrategyLiveDetailsPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      <Route path="/live-strategies" element={
        <ProtectedRoute>
          <LiveStrategiesPage />
        </ProtectedRoute>
      } />

      <Route path="/backtest" element={
        <ProtectedRoute>
          <MainLayout>
            <BacktestPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      <Route path="/backtest/:id/details" element={
        <ProtectedRoute>
          <MainLayout>
            <BacktestDetailsPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      {/* TradingPage - 1.0 */}
      {/* <Route path="/trading" element={
        <ProtectedRoute>
          <TradingPage />
        </ProtectedRoute>
      } /> */}

      <Route path="/ai-chat" element={
        <ProtectedRoute>
          <MainLayout>
            <AIChatPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      <Route path="/api-keys" element={
        <ProtectedRoute>
          <MainLayout>
            <APIManagementPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      {/*  */}
      <Route path="/api-management" element={
        <ProtectedRoute>
          <MainLayout>
            <APIManagementPage />
          </MainLayout>
        </ProtectedRoute>
      } />

      {/* TradingNotesPage - 1.0 */}
      {/* <Route path="/trading-notes" element={
        <ProtectedRoute>
          <MainLayout>
            <TradingNotesPage />
          </MainLayout>
        </ProtectedRoute>
      } /> */}

      <Route path="/profile" element={
        <ProtectedRoute>
          <MainLayout>
            <ProfilePage />
          </MainLayout>
        </ProtectedRoute>
      } />

      {/*  - MainLayout */}
      <Route path="/admin" element={
        <ProtectedRoute>
          <AdminDashboardPage />
        </ProtectedRoute>
      } />

      {/*  */}
      <Route path="/admin/users" element={
        <ProtectedRoute>
          <UserManagementPage />
        </ProtectedRoute>
      } />

      {/* Claude AI */}
      <Route path="/admin/claude" element={
        <ProtectedRoute>
          <ClaudeManagementPage />
        </ProtectedRoute>
      } />

      {/* USDT */}
      <Route path="/admin/wallets" element={
        <ProtectedRoute>
          <WalletManagementPage />
        </ProtectedRoute>
      } />
      
      {/*  */}
      <Route path="/admin/data" element={
        <ProtectedRoute>
          <DataManagementPageReal />
        </ProtectedRoute>
      } />

      {/* 404 */}
      <Route path="*" element={<NotFoundPage />} />
      </Routes>
    </GoogleOAuthProvider>
  )
}

export default App