/**
 * WebSocket AI
 * HTTPAI
 */

export interface WebSocketMessage {
  type: string
  [key: string]: any
}

export interface AIProgressUpdate {
  type: 'ai_progress_update'
  request_id?: string
  step: number
  total_steps: number
  status: string
  message: string
  timestamp?: string
}

export interface AIComplexityAnalysis {
  type: 'ai_complexity_analysis'
  request_id?: string
  complexity: 'simple' | 'medium' | 'complex'
  estimated_time_seconds: number
  message: string
}

export interface AIChatSuccess {
  type: 'ai_chat_success'
  request_id?: string
  response: string
  session_id?: string
  tokens_used: number
  model: string
  cost_usd: number
  message: string
}

export interface AIChatError {
  type: 'ai_chat_error'
  request_id?: string
  error: string
  error_code?: string
  complexity?: string
  retry_suggested?: boolean
  message: string
}

//  AI - 
export interface AIStreamStart {
  type: 'ai_stream_start'
  request_id?: string
  session_id?: string
  model?: string
  input_tokens?: number
}

export interface AIStreamChunk {
  type: 'ai_stream_chunk'
  request_id?: string
  chunk: string
  content_so_far: string  // 
  session_id?: string
}

export interface AIStreamEnd {
  type: 'ai_stream_end'
  request_id?: string
  session_id?: string
  content: string  // 
  tokens_used: number
  cost_usd: number
}

export interface AIStreamError {
  type: 'ai_stream_error'
  request_id?: string
  error: string
  error_type?: string
  message: string
  retry_suggested?: boolean
  timestamp?: string
}

export interface WebSocketConfig {
  url: string
  token: string
  reconnectInterval?: number
  maxReconnectAttempts?: number
  heartbeatInterval?: number
}

export type WebSocketEventHandler = (data: WebSocketMessage) => void

export interface WebSocketEvents {
  onOpen?: () => void
  onClose?: (event: CloseEvent) => void
  onError?: (event: Event) => void
  onAuthenticated?: (data: any) => void
  onAIStart?: (data: any) => void
  onComplexityAnalysis?: (data: AIComplexityAnalysis) => void
  onProgressUpdate?: (data: AIProgressUpdate) => void
  onAISuccess?: (data: AIChatSuccess) => void
  onAIError?: (data: AIChatError) => void
  onHeartbeat?: (data: any) => void
  onMessage?: (data: WebSocketMessage) => void
  //  AI
  onStreamStart?: (data: AIStreamStart) => void
  onStreamChunk?: (data: AIStreamChunk) => void
  onStreamEnd?: (data: AIStreamEnd) => void
  onStreamError?: (data: AIStreamError) => void
}

export class WebSocketAIClient {
  private ws: WebSocket | null = null
  private config: WebSocketConfig
  private events: WebSocketEvents = {}
  private isConnected = false
  private isAuthenticated = false
  private reconnectAttempts = 0
  private connectionId: string | null = null
  private userId: number | null = null
  private heartbeatTimer: NodeJS.Timeout | null = null
  private reconnectTimer: NodeJS.Timeout | null = null

  constructor(config: WebSocketConfig) {
    this.config = {
      reconnectInterval: 3000,
      maxReconnectAttempts: 5,
      heartbeatInterval: 30000,
      ...config
    }
  }

  /**
   * 
   */
  public on(events: WebSocketEvents): void {
    this.events = { ...this.events, ...events }
  }

  /**
   * WebSocket
   */
  public async connect(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      try {
        console.log(' WebSocket AI:', this.config.url)
        
        this.ws = new WebSocket(this.config.url)

        this.ws.onopen = () => {
          console.log(' WebSocket')
          this.isConnected = true
          this.reconnectAttempts = 0
          this.events.onOpen?.()
          this.authenticate().then(resolve).catch(reject)
        }

        this.ws.onclose = (event) => {
          console.log(' WebSocket:', event.code, event.reason)
          this.isConnected = false
          this.isAuthenticated = false
          this.connectionId = null
          this.userId = null
          this.clearTimers()
          this.events.onClose?.(event)
          
          // 
          if (event.code !== 1000) {
            this.handleReconnect()
          }
          
          // reject
          if (this.reconnectAttempts === 0) {
            reject(new Error(`WebSocket: ${event.reason || ''} (Code: ${event.code})`))
          }
        }

        this.ws.onerror = (event) => {
          console.error(' WebSocket:', event)
          this.events.onError?.(event)
          reject(event)
        }

        this.ws.onmessage = (event) => {
          try {
            const data = JSON.parse(event.data)
            
            // 
            if (!data || typeof data !== 'object') {
              console.error(' WebSocket:', data)
              return
            }
            
            // type
            if (!data.type) {
              console.error(' WebSockettype:', data)
              return
            }
            
            this.handleMessage(data)
          } catch (error) {
            console.error(' WebSocket:', error, 'Original data:', event.data)
          }
        }

      } catch (error) {
        console.error(' WebSocket:', error)
        reject(error)
      }
    })
  }

  /**
   * 
   */
  private async authenticate(): Promise<boolean> {
    return new Promise((resolve, reject) => {
      if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
        reject(new Error('WebSocket'))
        return
      }

      // 30
      const authTimeout = setTimeout(() => {
        console.error(' [WebSocketClient] 30')
        reject(new Error(''))
      }, 30000)

      // 
      const originalHandler = this.events.onAuthenticated
      console.log(' [WebSocketClient] ')
      
      // 
      this.events.onAuthenticated = (data) => {
        console.log(' [WebSocketClient] ', data)
        clearTimeout(authTimeout)
        this.isAuthenticated = true
        //  user_id connection_id
        this.connectionId = data.connection_id || `user_${data.user_id}_${Date.now()}`
        this.userId = data.user_id
        this.startHeartbeat()
        
        console.log(' WebSocket:', {
          connectionId: this.connectionId,
          userId: this.userId
        })
        
        // 
        this.events.onAuthenticated = originalHandler
        originalHandler?.(data)
        resolve(true)
      }

      // 
      const authMessage = {
        type: 'auth',  //  'auth'  'authenticate'
        token: this.config.token
      }
      console.log(' [WebSocketClient] :', { type: authMessage.type, tokenLength: authMessage.token?.length })
      this.send(authMessage)
    })
  }

  /**
   * AI
   */
  public async sendAIChat(
    content: string,
    aiMode: string = 'trader',
    sessionType: string = 'strategy',
    sessionId?: string
  ): Promise<string> {
    console.log(' [WebSocketClient] sendAIChat called:', {
      content: content.substring(0, 100) + '...',
      aiMode,
      sessionType,
      sessionId,
      isConnected: this.isConnected,
      isAuthenticated: this.isAuthenticated
    })
    
    if (!this.isConnected || !this.isAuthenticated) {
      const errorMsg = `WebSocket - :${this.isConnected}, :${this.isAuthenticated}`
      console.error(' [WebSocketClient]', errorMsg)
      throw new Error(errorMsg)
    }

    const requestId = this.generateRequestId()
    
    const message = {
      type: 'ai_chat',
      request_id: requestId,
      content,
      ai_mode: aiMode,
      session_type: sessionType,
      session_id: sessionId
    }
    
    console.log(' [WebSocketClient] AI:', message)
    this.send(message)

    return requestId
  }

  /**
   * AI
   */
  public cancelAIRequest(requestId: string): void {
    if (!this.isConnected || !this.isAuthenticated) return

    this.send({
      type: 'cancel_request',
      request_id: requestId
    })
  }

  /**
   * 
   */
  public ping(): void {
    if (!this.isConnected) return

    this.send({
      type: 'ping'
    })
  }

  /**
   * 
   */
  public disconnect(): void {
    this.clearTimers()
    
    if (this.ws) {
      this.ws.close()
      this.ws = null
    }
    
    this.isConnected = false
    this.isAuthenticated = false
    this.connectionId = null
    this.userId = null
  }

  /**
   * 
   */
  public getConnectionInfo() {
    return {
      isConnected: this.isConnected,
      isAuthenticated: this.isAuthenticated,
      connectionId: this.connectionId,
      userId: this.userId,
      reconnectAttempts: this.reconnectAttempts
    }
  }

  // ==========  ==========

  private handleMessage(data: WebSocketMessage): void {
    //  - Object
    const logMessage = {
      type: data.type,
      request_id: data.request_id || 'N/A',
      ...(data.type === 'ai_progress_update' && {
        step: data.step,
        total_steps: data.total_steps,
        status: data.status,
        message: data.message
      }),
      ...(data.type === 'ai_stream_chunk' && {
        chunk_length: data.chunk?.length || 0
      }),
      ...(data.type === 'ai_stream_end' && {
        response_length: data.content?.length || 0,  // 
        tokens_used: data.tokens_used,
        cost_usd: data.cost_usd
      })
    }
    console.log(' WebSocket:', logMessage)

    switch (data.type) {
      case 'connection_established':
        console.log(' [WebSocketClient] :', data)
        this.isAuthenticated = true  // 
        this.connectionId = data.connection_id || null
        this.userId = data.user_id || null
        // 
        if (this.events.onAuthenticated) {
          console.log(' [WebSocketClient] onAuthenticated (connection_established)')
          this.events.onAuthenticated(data)
        }
        break
        
      case 'auth_success':
        console.log(' [WebSocketClient] :', data)
        this.isAuthenticated = true
        this.connectionId = data.connection_id || null
        this.userId = data.user_id || null
        // 
        if (this.events.onAuthenticated) {
          console.log(' [WebSocketClient] onAuthenticated')
          this.events.onAuthenticated(data)
        } else {
          console.warn(' [WebSocketClient] onAuthenticated')
        }
        break
      
      case 'ai_chat_start':
        this.events.onAIStart?.(data)
        break
      
      case 'ai_complexity_analysis':
        this.events.onComplexityAnalysis?.(data as AIComplexityAnalysis)
        break
      
      case 'ai_progress_update':
        // 
        this.events.onProgressUpdate?.(data as AIProgressUpdate)
        break
      
      case 'ai_chat_success':
        this.events.onAISuccess?.(data as AIChatSuccess)
        break
      
      case 'ai_chat_error':
        this.events.onAIError?.(data as AIChatError)
        break
      
      //  AI
      case 'ai_stream_start':
        console.log(' [WebSocketClient] AI:', data)
        this.events.onStreamStart?.(data as AIStreamStart)
        break
      
      case 'ai_stream_chunk':
        console.log(' [WebSocketClient] AI:', data?.chunk || '[chunk]')
        this.events.onStreamChunk?.(data as AIStreamChunk)
        break
      
      case 'ai_stream_end':
        console.log(' [WebSocketClient] AI:', data)
        console.log(' [WebSocketClient] onStreamEnd')
        
        if (this.events.onStreamEnd) {
          console.log(' [WebSocketClient] onStreamEnd...')
          this.events.onStreamEnd(data as AIStreamEnd)
          console.log(' [WebSocketClient] onStreamEnd')
        } else {
          console.log(' [WebSocketClient] onStreamEnd!')
        }
        break
      
      case 'ai_stream_error':
        //  Object 
        const errorInfo = {
          error: data?.error || 'Unknown error',
          error_type: data?.error_type || 'UNKNOWN',
          message: data?.message || 'No error message',
          request_id: data?.request_id || 'No request ID'
        }
        console.log(' [WebSocketClient] AI:', errorInfo)
        console.log(' [WebSocketClient] :', JSON.stringify(data, null, 2))
        this.events.onStreamError?.(data as AIStreamError)
        break
      
      case 'heartbeat':
      case 'pong':
        this.events.onHeartbeat?.(data)
        break
      
      default:
        this.events.onMessage?.(data)
        break
    }
  }

  private send(data: any): void {
    if (!this.ws || this.ws.readyState !== WebSocket.OPEN) {
      console.warn(' WebSocket:', data)
      return
    }

    try {
      this.ws.send(JSON.stringify(data))
      console.log(' WebSocket:', data.type)
    } catch (error) {
      console.error(' WebSocket:', error)
    }
  }

  private startHeartbeat(): void {
    if (this.heartbeatTimer) {
      clearInterval(this.heartbeatTimer)
    }

    this.heartbeatTimer = setInterval(() => {
      this.ping()
    }, this.config.heartbeatInterval!)
  }

  private clearTimers(): void {
    if (this.heartbeatTimer) {
      clearInterval(this.heartbeatTimer)
      this.heartbeatTimer = null
    }
    
    if (this.reconnectTimer) {
      clearTimeout(this.reconnectTimer)
      this.reconnectTimer = null
    }
  }

  private handleReconnect(): void {
    if (this.reconnectAttempts >= this.config.maxReconnectAttempts!) {
      console.error(' ')
      return
    }

    this.reconnectAttempts++
    console.log(`  (${this.reconnectAttempts}/${this.config.maxReconnectAttempts})`)

    this.reconnectTimer = setTimeout(() => {
      this.connect().catch(console.error)
    }, this.config.reconnectInterval!)
  }

  private generateRequestId(): string {
    return Math.random().toString(36).substring(2) + Date.now().toString(36)
  }
}

export default WebSocketAIClient